@font-face {
    font-family: 'DiodrumArabic-Light';
    src: url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light.eot');
    src: url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light.eot?#iefix') format('embedded-opentype'),
        url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light.woff2') format('woff2'),
        url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light.woff') format('woff'),
        url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light') format('truetype'),
        url('http://localhost/mowazi/wp-content/themes/mowazi/fonts/DiodrumArabic-Light.svg##DiodrumArabic-Light') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

